:root {
  --basesize: 1.05rem;
  --basefamily:  'ao', Georgia, serif;
  --basesize: 1.15rem;
  --basefamily:  Georgia, serif;
  --line-height: 1.4;
  --text-color: rgba(0,0,0,.75); }

body {
  font-family: var(--basefamily);
  font-size: var(--basesize);
  color: var(--text-color);
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: var(--line-height); }

h1, h2, h3 {
  font-weight: 400; }

p, ul, ol, blockquote {
  max-width: 75ch; }

:root {
  --background-color: #eee;
  --padding: 1em;
  --margin: 2em; }
  @media (max-width: 800px) {
    :root {
      --padding: 1em;
      --margin: 1em; } }

*, *::before, *::after {
  box-sizing: border-box; }

html, body {
  scroll-behavior: smooth; }

a {
  color: currentColor;
  text-decoration: none; }

figure {
  margin: 0;
  padding: 0; }

img {
  max-width: 100%;
  height: auto; }

html {
  scroll-behavior: smooth; }

@media (prefers-reduced-motion: reduce) {
  html {
    scroll-behavior: auto; } }

#home {
  --year-width: 4rem;
  --line-offset: 1rem;
  --event-padding: 1rem;
  --grid-gap: 2rem;
  margin: var(--padding);
  padding: var(--padding);
  padding-left: var(--year-width); }
  @media (max-width: 800px) {
    #home {
      margin: 0;
      padding-left: var(--padding); } }

#annee-1960 > h2,
#annee-1970 > h2,
#annee-1980 > h2,
#annee-1990 > h2,
#annee-2000 > h2,
#annee-2010 > h2,
#annee-2020 > h2 {
  font-weight: bold;
  font-size: 150%;
  position: relative;
  top: -.6em; }

#home > header {
  grid-gap: 0 var(--grid-gap);
  display: grid;
  grid-template-columns: 2fr 3fr 2fr;
  margin: 2em 0; }
  @media (max-width: 800px) {
    #home > header {
      grid-template-columns: 1fr; } }
  #home > header h2 {
    margin: 0;
    color: var(--color);
    line-height: 1.2;
    font-weight: 400;
    font-size: clamp(1.5rem, 2.5vw, 2.5rem); }
  #home > header > div {
    padding-left: calc( var(--line-offset) + var(--event-padding)); }
    #home > header > div.edition-et-culture-graphique {
      text-align: right; }
      @media (max-width: 800px) {
        #home > header > div.edition-et-culture-graphique {
          text-align: left; } }
    @media (max-width: 800px) {
      #home > header > div {
        padding-left: 0; } }
    #home > header > div p {
      margin-top: .25em;
      font-style: italic; }

.year {
  border-top: 1px dotted #000;
  position: relative;
  min-height: 2em;
  display: grid;
  grid-template-columns: 2fr 3fr 2fr;
  grid-auto-flow: dense;
  grid-gap: 0 var(--grid-gap);
  scroll-margin-top: 7em; }
  .year:target {
    margin-top: 6em; }
  @media (max-width: 800px) {
    .year {
      grid-template-columns: 1fr;
      margin-top: var(--margin); } }
  .year span {
    border-right: 2px solid var(--color);
    width: var(--line-offset);
    display: block;
    position: absolute;
    height: 100%; }
    .year span:nth-of-type(1) {
      grid-column: 1 / span 1; }
    .year span:nth-of-type(2) {
      grid-column: 2 / span 1; }
    .year span:nth-of-type(3) {
      grid-column: 3 / span 1; }
  .year > h2 {
    margin: 0;
    position: absolute;
    top: -.65em;
    font-size: 1em;
    line-height: 1;
    background: var(--background-color);
    padding: 0 1em 0 0;
    width: var(--year-width);
    left: calc(-1 * var(--year-width)); }
    @media (max-width: 800px) {
      .year > h2 {
        left: 0; } }

.year-content {
  scroll-margin-top: 7em; }

.events.edition-et-culture-graphique {
  grid-column: 1; }

.events.les-rencontres-de-lure {
  grid-column: 2; }

.events.typographie-et-technique {
  grid-column: 3; }

@media (max-width: 800px) {
  .events {
    grid-column: 1 !important; } }

.timeline-event {
  padding: var(--event-padding) 0 var(--event-padding) calc( var(--line-offset) + var(--event-padding));
  position: relative; }
  @media (max-width: 800px) {
    .timeline-event {
      padding-left: 0; } }
  .timeline-event h3 {
    color: var(--color, currentColor);
    margin-bottom: .25em; }
  .timeline-event.edition-et-culture-graphique {
    grid-column: 1;
    text-align: right; }
    @media (max-width: 800px) {
      .timeline-event.edition-et-culture-graphique {
        text-align: left; } }
  .timeline-event.les-rencontres-de-lure {
    grid-column: 2; }
  .timeline-event.typographie-et-technique {
    grid-column: 3; }
  .timeline-event::before {
    content: "";
    display: block;
    height: 1rem;
    width: 1rem;
    border-radius: .5rem;
    position: absolute;
    left: calc(var(--line-offset) - 1px);
    top: calc(.5rem + var(--event-padding));
    transform: translateX(-50%);
    background: var(--color, red);
    border: 3px solid var(--background-color);
    display: none; }
    @media (max-width: 800px) {
      .timeline-event::before {
        display: none; } }
  .timeline-event :first-child {
    margin-top: 0; }
  .timeline-event .introduction {
    font-size: .85em; }

.source {
  font-size: .85em;
  text-decoration: underline; }

.event-cover {
  margin: 0;
  padding: 0; }
  .event-cover img {
    display: block; }

.event-detail,
.gallery {
  margin: 40px 0;
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 40px; }
  @media (max-width: 800px) {
    .event-detail,
    .gallery {
      grid-template-columns: 1fr; } }
  .event-detail a,
  .gallery a {
    text-decoration: underline; }

body > .main-event {
  margin: 0 var(--margin); }

.event-detail {
  grid-gap: 0 40px; }
  .event-detail header {
    grid-column: span 6;
    position: relative; }
    @media (max-width: 800px) {
      .event-detail header {
        grid-column: span 1; } }
    .event-detail header h2 {
      margin: 0; }
    .event-detail header h1 {
      margin-top: 0;
      color: var(--color); }
    .event-detail header button {
      position: absolute;
      right: 0;
      top: 0;
      border: none;
      background: transparent;
      font-size: 2rem;
      cursor: pointer; }
  .event-detail .event-text {
    grid-column: span 4; }
    @media (max-width: 800px) {
      .event-detail .event-text {
        grid-column: span 1; } }
  .event-detail .event-details {
    grid-column: span 2;
    font-size: .85em; }
    @media (max-width: 800px) {
      .event-detail .event-details {
        grid-column: span 1; } }
    .event-detail .event-details h1, .event-detail .event-details h2, .event-detail .event-details h3, .event-detail .event-details h4 {
      font-size: 1em;
      font-weight: bold;
      color: var(--color); }

.gallery img {
  display: block; }

.gallery figcaption {
  --padding: 1.5em;
  font-size: .85em;
  padding-left: var(--padding); }
  .gallery figcaption p:first-child::before {
    content: "↑ ";
    width: var(--padding);
    margin-left: calc(-1 * var(--padding));
    display: inline-block; }

.gallery .two_of_three {
  grid-column: span 4; }

.gallery .one_of_two {
  grid-column: span 3; }

.gallery .one_of_one {
  grid-column: span 6; }

.gallery .one_of_three {
  grid-column: span 2; }

@media (max-width: 800px) {
  .gallery .two_of_three {
    grid-column: span 1; }
  .gallery .one_of_two {
    grid-column: span 1; }
  .gallery .one_of_one {
    grid-column: span 1; }
  .gallery .one_of_three {
    grid-column: span 1; } }

body {
  margin: 0;
  background: var(--background-color); }

#header {
  padding: var(--padding);
  z-index: 3;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  border-bottom: 1px solid;
  background: var(--background-color); }
  @media (max-width: 800px) {
    #header {
      position: static; } }
  #header > div {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%; }
    @media (max-width: 1024px) {
      #header > div {
        flex-direction: column;
        align-items: flex-start; } }
  #header h1 {
    font-size: 1em;
    font-weight: 900;
    margin: 0; }
  #header p {
    margin: 0; }

@media (max-width: 1024px) {
  #years-nav {
    margin: var(--padding) calc(-1 * var(--padding)) 0;
    border-top: 1px solid;
    padding: var(--padding) var(--padding) 0;
    width: calc( 100% + var(--padding) *2); } }

#years-nav ul {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0; }

#years-nav li {
  margin-right: 1em; }

#years-nav a {
  border-bottom: 1px solid transparent; }
  #years-nav a:hover {
    border-color: #000; }
